<template>
  <div class="baima-container">
    <advert></advert>
    <div class="forgot-container">
      <language></language>
      <div class="forgot-inner">
        <el-form class="forgot-form" autoComplete="on" :model="forgotForm" :rules="forgotRules" ref="forgotForm"
                 label-position="left">
          <el-form-item :label="$t('forgotPassword.telOrEmail')" prop="username" :class="{'noLabel': !forgotForm.username, 'onFocus':forgotType.username === 1}">
            <el-input name="username" type="text" v-model="forgotForm.username"
                      :placeholder="$t('forgotPassword.telOrEmail')" @focus="forgotType.username = 1" @blur="forgotType.username = 0"/>
          </el-form-item>
          <el-form-item :label="$t('forgotPassword.VerificationCode')" class="verificationCode" prop="verificationCode" :class="{'noLabel': !forgotForm.verificationCode, 'onFocus':forgotType.verificationCode === 1}">
            <el-input name="verificationCode" type="text" v-model="forgotForm.verificationCode"
                      :placeholder="$t('forgotPassword.VerificationCode')" @focus="forgotType.verificationCode = 1" @blur="forgotType.verificationCode = 0"/>
            <el-button type="primary" :disabled="!isUsername" @click="sendCode">
              <span v-show="!countShow">
                <template v-if="isFirstSend">{{$t('forgotPassword.sendCode')}}</template>
                <template v-else>{{$t('forgotPassword.sendCodeRe')}}</template>
              </span>
              <span v-show="countShow">{{count}} 秒</span>
            </el-button>
          </el-form-item>
          <el-form-item :label="$t('forgotPassword.newPass')" prop="password" :class="{'noLabel': !forgotForm.password, 'onFocus':forgotType.password === 1}">
            <el-input name="password" :type="pwdType" @keyup.enter.native="handleLogin" v-model="forgotForm.password"
                      :placeholder="$t('forgotPassword.newPass')" @focus="forgotType.password = 1" @blur="forgotType.password = 0"></el-input>
            <span class="show-pwd" @click="showPwd" v-if="forgotForm.password">
            <img src="../../images/ico-pass_hidden.png" width="24" alt="" v-if="pwdType">
            <img src="../../images/ico-pass_show.png" width="24" alt="" v-else>
          </span>
          </el-form-item>
          <el-form-item :label="$t('forgotPassword.checkPass')" prop="checkPassword" :class="{'noLabel': !forgotForm.checkPassword, 'onFocus':forgotType.checkPassword === 1}">
            <el-input name="checkPassword" :type="pwdType" @keyup.enter.native="handleLogin"
                      v-model="forgotForm.checkPassword"
                      :placeholder="$t('forgotPassword.checkPass')" @focus="forgotType.checkPassword = 1" @blur="forgotType.checkPassword = 0"></el-input>
            <span class="show-pwd" @click="showPwd" v-if="forgotForm.checkPassword">
            <img src="../../images/ico-pass_hidden.png" width="24" alt="" v-if="pwdType">
            <img src="../../images/ico-pass_show.png" width="24" alt="" v-else>
          </span>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleFinish"
                       :disabled="!isUsername || !isCode || !isPassword || !isCheckPassword">
              {{$t('forgotPassword.finish')}}
            </el-button>
          </el-form-item>
        </el-form>
        <dl>
          <dt>
            <el-button type="text" @click="goHome">{{$t('forgotPassword.goHome')}}</el-button>
          </dt>
          <dd></dd>
        </dl>
      </div>
      <copy-right></copy-right>
    </div>
  </div>
</template>

<script>
import { isvalidUsername, isvalidUserpassword } from 'common/js/validate'
import Advert from 'base/advert'
import Language from 'base/language'
import copyRight from 'base/copyright'
import { setToken } from 'common/js/auth'

const TIME_COUNT = 59

export default {
  name: 'ForgotPassword',
  data () {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        this._data.isUsername = false
        callback(new Error(' '))
      } else {
        this._data.isUsername = true
        callback()
      }
    }
    const validateCode = (rule, value, callback) => {
      if(value.length !== 4) {
        this._data.isCode = false
        callback(new Error(' '))
      } else {
        this._data.isCode = true
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (!isvalidUserpassword(value)) {
        this._data.isPassword = false
        callback(new Error(' '))
      } else {
        this._data.isPassword = true
        callback()
      }
    }
    const validateCheckPass = (rule, value, callback) => {
      if (!isvalidUserpassword(value)) {
        this._data.isCheckPassword = false
        callback(new Error(' '))
      } else if(value !== this.forgotForm.password) {
        this._data.isCheckPassword = false
        callback(new Error(' '))
      } else {
        this._data.isCheckPassword = true
        callback()
      }
    }
    return {
      forgotForm: {
        username: '',
        verificationCode: '',
        password: '',
        checkPassword: ''
      },
      // 0：无 1：输入中 2：出错：3：完成
      forgotType: {
        username: '',
        verificationCode: '',
        password: '',
        checkPassword: ''
      },
      forgotRules: {
        username: [
          {required: true, trigger: 'blur', validator: validateUsername},
          {required: true, trigger: 'change', validator: validateUsername}
        ],
        verificationCode: [
          {required: true, trigger: 'blur', validator: validateCode},
          {required: true, trigger: 'change', validator: validateCode}
        ],
        password: [
          {required: true, trigger: 'blur', validator: validatePass},
          {required: true, trigger: 'change', validator: validatePass}
        ],
        checkPassword: [
          {required: true, trigger: 'blur', validator: validateCheckPass},
          {required: true, trigger: 'change', validator: validateCheckPass}
        ]
      },
      loading: false,
      pwdType: 'password',

      isUsername: '',
      isCode: '',
      isPassword: '',
      isCheckPassword: '',
      isDisabled: true,

      isFirstSend: true, // 判断是不是没点过发送，因为提示的文字不一样
      count: '', // 内部显示的数字
      countShow: false, // 判断显示的是文字提示还是倒计时数字
      timer: null  // 计时器
    }
  },
  created () {
  },
  methods: {
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    handleFinish () {
      this.$refs.forgotForm.validate(valid => {
        if (valid) {
          this.loading = true
          // let userInfo = Object.assign({}, this.loginForm)
          // 调完成接口
          // loginByUsername({userInfo}).then((res) => {
          //   this.loading = false
          //   this.$store.commit('SET_TOKEN', 'admin')
          //   // 模拟权限
          //   setToken('admin')
          //   this.$router.push({path: '/'})
          // }).catch(() => {
          //   this.loading = false
          // })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 发送验证码
    sendCode() {
      if(!this.timer) {
        this.count = TIME_COUNT
        this.countShow = true
        this.isFirstSend = false
        // ajax调验证码接口位置

        // 定时器60秒
        this.timer = setInterval(() => {
          if(this.count > 0 && this.count <= TIME_COUNT) {
            this.count--
          } else {
            this.countShow = false
            clearInterval(this.timer)
            this.timer = null
          }
        }, 1000)
      }
    },
    // 返回首页
    goHome () {
      this.$router.go('-1')
    }
  },
  destroyed() {
    clearInterval(this.timer)
    this.timer = null
  },
  components: {
    Advert,
    Language,
    copyRight
  }
}


</script>

<style rel="stylesheet/scss" lang="scss">
@import "common/styles/variable.scss";
$bg: #2d3a4b;
$light_gray: #eee;

/* reset element-ui css */
.forgot-container {
  .forgot-form {
    .el-form-item {
      border: none;
      margin-bottom: 8px;
      &:nth-of-type(2) {
        .el-input {
          width: 220px;
        }
        .el-button {
          width: 136px;
          font-size: 14px;
          margin-left: 6px;
          padding: 11px 5px;
        }
      }
      &:nth-of-type(4) {
        margin-bottom: 36px;
      }
      &:nth-of-type(5) {
        margin-bottom: 2px;
      }
      &.noLabel label {
        visibility: hidden;
      }
      & label::before {
        display: none;
      }
      .el-form-item__content {
        clear: both;
      }
      .el-input {
        display: inline-block;
        height: 36px;
        input {
          background: transparent;
          border: 0px;
          -webkit-appearance: none;
          border-radius: 0px;
          padding: 9px 5px 9px 0;
          color: $gray_dark;
          height: 36px;
          font-size: 18px;
          border-bottom: 1px solid $gray_line;
          &::-webkit-input-placeholder {
            color: $gray_placeholder;
          }
        }
      }
      .el-input__inner:focus {
        border-bottom: 1px solid $gray_line_on;
      }
      label {
        color: $gray_placeholder;
        font-size: 12px;
        line-height: 12px;
        padding: 8px 0 0 0;
      }
      &.is-error {
        label {
          visibility: visible;
          color: $red;
        }
        .el-input__inner {
          border-bottom: 1px solid $red;
        }
        .el-input__inner:focus {
          border-bottom: 1px solid $gray_line_on;
        }
      }
      &.onFocus {
        label {
          visibility: visible;
          color: $gray_line_on;
        }
      }
      .el-button {
        padding: 15px 20px;
        font-size: 18px;
        background-color: $gray_line_on;
        border: none;
        border-radius: 4px;
      }
      .el-button--medium {
        font-size: 18px;
        color: $white;
      }
      .is-disabled {
        background: $gray_line;
        color: $gray_light;
      }
    }
  }
  .forgot-inner {
    .el-button--medium {
      font-size: 12px;
      color: $blue;
    }
  }
}

</style>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "common/styles/variable.scss";
.baima-container {
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  .forgot-container {
    flex: 1;
    position: relative;
    .forgot-inner {
      position: absolute;
      left: 50%;
      right: 0;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 366px;
      margin: 0 auto;
      .show-pwd {
        position: absolute;
        right: 0;
        top: 10px;
        height: 24px;
        cursor: pointer;
        user-select: none;
      }
      dl {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        dt {
          flex: 1;
        }
        dd {
          flex: 1;
          color: $gray_light;
          text-align: right;
        }
      }
    }
  }
}
</style>



